.btn {
  border: none;
  background-color: $color-gray-dark;
  color: $color-text-white;
  border-radius: 5px;
  line-height: 5.4rem;
  font-weight: 700;
  font-size: 1.8rem;
  font-family: "Roboto", sans-serif;
  &:hover {
    background-color: $color-border-accent;
    transition: 0.4s;
  }
  &-inActive {
    background-color: $color-gray-light;
    &:hover {
      background-color: $color-gray-light;
    }
  }
}
